

/* Global Styles */
body {
  font-family: 'Times New Roman', serif;
  background-color: #000;
  color: #f0f0f0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

/* Aesthetic Highlight Styles */
.highlight {
  font-weight: bold;
  color: #ffcc00;
  text-shadow: 2px 2px 8px rgba(255, 204, 0, 0.5);
}

/* Aesthetic Links */
a {
  color: #ffcc00;
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: #f5a623;
  text-decoration: underline;
}

/* General Section Styling */
section {
  padding: 80px 5%;
  text-align: center;
  min-height: 100vh;
}

/* Advanced Background Design for All Sections Except Home */
.About, .Contact, .Projects, .Skills, .Education {
  background: linear-gradient(135deg, rgba(30, 30, 30, 0.9), rgba(40, 40, 40, 0.9)), 
              radial-gradient(circle at top right, #ffcc00 10%, transparent 70%);
  background-blend-mode: overlay;
  position: relative;
}

.About::before, .Contact::before, .Projects::before, .Skills::before, .Education::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: repeating-linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0.1) 10px,
    transparent 10px,
    transparent 20px
  );
  opacity: 0.05;
  z-index: -1;

}


/* Shadowed Text for Section Titles */
h1, h2, h3 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  text-shadow: 0px 4px 10px rgba(255, 204, 0, 0.4);
  color: #ffcc00;
}

h2.highlight {
  font-size: 3rem;
  margin-bottom: 40px;
}

p {
  font-size: 1.2rem;
  line-height: 1.6;
  margin-bottom: 40px;
  color: #f0f0f0;
}

/* Navbar Styling */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 15px 0;
  z-index: 1000;
  border-bottom: 1px solid #444;
  transition: background-color 0.4s ease;
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.navbar a {
  color: #fff;
  padding: 10px 20px;
  font-size: 1.2rem;
  border-radius: 5px;
}

.navbar a:hover {
  background-color: #ffcc00;
  color: #000;
  transition: 0.3s ease-in-out;
}



/* Home Section Styling */
.home {
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
  background: url('./assets/background.jpg') no-repeat center center;
  background-size: cover;
}


.home h1 {
  font-size: 4rem;
  animation: fadeInDown 1s ease-in-out;
}

.home h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #ffcc00;
  animation: fadeInDown 1s ease-in-out;
}

.home p {
  font-size: 1.5rem;
  max-width: 600px;
  margin: 20px auto;
  animation: fadeInDown 1s ease-in-out;
}

/* Skills Section Styling */
.Skills {
  animation: fadeInDown 1s ease-in-out;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 30px;
  justify-items: center;
  padding-top: 40px;
}

.skill-card {
  background-color: rgba(34, 34, 34, 0.95);
  padding: 20px;
  border-radius: 15px;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
  width: 180px;
  height: 220px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.skill-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 20px rgba(255, 204, 0, 0.3);
}

.skill-logo {
  width: 80px;
  height: 80px;
  margin-bottom: 15px;
}

.skills p {
  margin-top: 10px;
  font-size: 1.2rem;
  font-weight: bold;
  color: #ffcc00;
}

/* Projects Section Styling */
.Projects  {
  animation: fadeInDown 1s ease-in-out;

}

.projects {
  color: #fff;
}

.project-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}

.project-card {
  background-color: #111;
  border: 1px solid #444;
  padding: 30px;
  border-radius: 10px;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s;
  width: 300px;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 20px rgba(255, 204, 0, 0.3);
}

.project-card h3 {
  margin-bottom: 15px;
}

.project-card p {
  font-size: 1rem;
  line-height: 1.5;
  color: #ccc;
}

/* Center the content within the education section */
#education {
  text-align: center;
  padding: 40px 20px;
}

#education h1 {
  font-size: 3rem;
  color: #FFD700; /* Gold color for the section title */
  margin-bottom: 20px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.education-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.education-content ul {
  list-style-type: none; /* Removes bullet points */
  padding: 0;
}

.education-content li {
  font-size: 1.2rem;
  color: #FFFFFF;
  text-align: left;
  max-width: 800px; /* Constrains the width */
  margin: 10px 0;
  padding: 10px 20px;
  background: rgba(0, 0, 0, 0.6); /* Semi-transparent background */
  border-radius: 8px;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
}

.education-content li strong {
  color: #FFD700; /* Matches the title color */
}

#education {
  min-height: 100vh; /* Make the section cover the full viewport height */
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center content vertically */
  background: linear-gradient(to bottom, black, orange); /* Keep your gradient */
  padding: 20px; /* Optional padding for spacing */
}

/* Contact Section Styling */
.Contact {
  animation: fadeInDown 1s ease-in-out;
}


.contact p {
  font-size: 1.2rem;
  margin: 10px 0;
}

.contact a {
  color: #ffcc00;
  text-decoration: none;
}

.contact a:hover {
  text-decoration: underline;
}

/* Responsive Styling */
@media (max-width: 768px) {
  h1, h2 {
    font-size: 2rem;
  }

  .navbar ul {
    flex-direction: column;
    gap: 10px;
  }

  .project-grid {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  h1, h2 {
    font-size: 1.5rem;
  }

  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }

  .project-card {
    width: 100%;
  }
}

/* Fade-in animation class */
.fade-in {
  opacity: 0;
  animation: fadeIn 1s ease-in-out forwards; /* `forwards` keeps the final state */
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* Initial hidden state */
section {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s ease-out, transform 1s ease-out;
}

/* Fade-in class that is applied when the section comes into view */
section.fade-in {
  opacity: 1;
  transform: translateY(0);
}
@media (max-width: 768px) {
  /* Styles for mobile devices */
  .About .Contact .Education .Projects .Skills .home {
      flex-direction: column; /* Stack items vertically */
  }
}

/* Main heading */
h1 {
  font-size: 8vw; /* Adjust this value as needed */
}

/* Subheading or intro text */
.intro-text {
  font-size: 5vw; /* Adjust this value as needed */
}
/* Default styling */
h1 {
  font-size: 6vw;
}

.intro-text {
  font-size: 4vw;
}

/* Apply different sizes for smaller screens */
@media (max-width: 768px) {
  h1 {
      font-size: 8vw;
  }

  .intro-text {
      font-size: 6vw;
  }
}

/* Even smaller screens */
@media (max-width: 480px) {
  h1 {
      font-size: 10vw;
  }

  .intro-text {
      font-size: 8vw;
  }
}
